import request from '@/utils/request'

export default {
  // 
  getFavoriteListData (userId) {
    return request.get(`/api/v1/qp/favorites?current_user_id=${userId}`)
  },
  // 
  createFavorite (params) {
    return request.post('/api/v1/qp/favorites', params)
  },
  // 
  getFavorite (id) {
    return request.get(`/api/v1/qp/favorites/${id}`)
  },
  // 
  deleteFavorite (id) {
    return request.delete(`/api/v1/qp/favorites/${id}`)
  }
}
