// import axios from 'axios'
import router from '@/router'
import store from '@/store'
import Cookies from 'js-cookie'

/**
 * axios
 * vuextrue(v-loading)
 * vuexfalse(v-loading)
 */
axios.interceptors.request.use(
  config => {
    // config.timeout = 5000
    config.baseURL = process.env.remoteUrls.ERP
    config.headers.remoteSystem = 'QP'
    config.headers.Authorization = `Bearer ${Cookies.get('qp_access_token')}`
    // store.dispatch('root/setDataLoading', true)
    return config
  },
  error => {
    // @TODO: loading
    // setTimeout(() => store.dispatch('root/setDataLoading', false), 500)
    store.dispatch('root/setDataLoading', false)
    return Promise.reject(error)
  }
)

axios.interceptors.response.use(
  res => {
    store.dispatch('root/setDataLoading', false)
    if (res.data.success === false && router.currentRoute.name !== 'Login') {
      // APItoken
      store.dispatch('root/systemLogout', '')
    }
    if (res.status !== 200) {
      return Promise.reject(res)
    }
    return res
  },
  error => {
    store.dispatch('root/setDataLoading', false)
    if (error.response.status === 401 && router.currentRoute.name !== 'Login') {
      // API401
      store.dispatch('root/systemLogout', '')
    }
    return Promise.reject(error)
  }
)

export default axios
