import request from '@/utils/request'

export default {
  // 
  getOptionListData () {
    return request.get('/api/v1/qp/conditions/options')
  },
  // 
  getClientListData () {
    return request.get('/api/v1/qp/infos/qp_client_list')
  },
  // 
  getAgencyListData () {
    return request.get('/api/v1/qp/infos/qp_agency_list')
  },
  // 
  createQuickPlanInfo (params) {
    return request.post('/api/v1/qp/infos', params)
  },
  // 
  doCalculation (id) {
    return request.post('/api/v1/qp/sim_results/data', { qp_condition_id: id })
  }
}
