<!--  -->
<template>
  <div class="demographic">
    <div class="option">
      <span class="option-title required"></span>
      <span
        class="option-warning"
        v-if="warningText">
        {{ warningText }}
      </span>
      <!--  -->
      <div class="option-content">
        <div class="option-content-name"></div>
        <el-checkbox
          class="option-item"
          @change="toggleAllMale"
          :value="isCheckedAllMale">
          
        </el-checkbox>
        <el-checkbox
          class="option-item"
          @change="toggleAllFemale"
          :value="isCheckedAllFemale">
          
        </el-checkbox>
      </div>
      <!--  -->
      <el-checkbox-group v-model="demographics">
        <div
          class="option-content"
          v-for="index in optionsLength"
          :key="index">
          <!--  -->
          <div class="option-content-name">{{ sortedOptions.male[index -1].name_cn }}</div>
          <!--  -->
          <el-checkbox
            class="option-item"
            :label="sortedOptions.male[index -1].id">
            
          </el-checkbox>
          <el-checkbox
            class="option-item"
            :label="sortedOptions.female[index -1].id">
            
          </el-checkbox>
        </div>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Demographic',
  props: {
    options: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      demographics: []
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    // 
    warningText () {
      return this.demographics.length < 2 ? '' : null
    },
    // 
    optionsLength () {
      return this.sortedOptions.male.length
    },
    // 
    sortedOptions () {
      const options = this.$lodash.cloneDeep(this.options)
      return {
        male: options.male.sort((a, b) => a.abbrev.localeCompare(b.abbrev)),
        female: options.female.sort((a, b) => a.abbrev.localeCompare(b.abbrev))
      }
    },
    // id
    allMaleId () {
      return this.options.male.map(option => option.id)
    },
    // id
    allFemaleId () {
      return this.options.female.map(option => option.id)
    },
    // 
    isCheckedAllMale () {
      return this.allMaleId.every(id => this.demographics.includes(id))
    },
    // 
    isCheckedAllFemale () {
      return this.allFemaleId.every(id => this.demographics.includes(id))
    }
  },
  watch: {
    demographics: {
      handler () {
        this.updateData()
      },
      immediate: true
    },
    options: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 
    setDefaultData () {
      const dataSource = this.$lodash.cloneDeep(this.dataSource.demographics)
      this.demographics = dataSource || [...this.allMaleId, ...this.allFemaleId]
    },
    // 
    toggleAllMale (isChecked) {
      isChecked
        ? this.allMaleId.forEach(id => this.demographics.push(id))
        : this.allMaleId.forEach(id => this.demographics.splice(this.demographics.indexOf(id), 1))
      this.demographics = [...new Set(this.demographics)]
    },
    // 
    toggleAllFemale (isChecked) {
      isChecked
        ? this.allFemaleId.forEach(id => this.demographics.push(id))
        : this.allFemaleId.forEach(id => this.demographics.splice(this.demographics.indexOf(id), 1))
      this.demographics = [...new Set(this.demographics)]
    },
    // 
    updateData () {
      this.updateRequireCheckData({ key: 'demographics', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'demographics', value: this.demographics },
        { key: 'condition.demo_ids', value: this.demographics }
      ])
    }
  }
}
</script>
