<!-- () -->
<template>
  <div class="favorite">
    <div class="favorite-tool fixed" :class="{ 'show': showToolBar}">
      <el-button
        class="btn-reset"
        @click="resetConfirm">
        
      </el-button>
      <el-button
        class="btn-load"
        @click="openFavoriteList = true"
        type="primary">
        
      </el-button>
      <el-input class="favorite-name" v-model="favoriteName"></el-input>
      <el-button
        class="btn-save"
        @click="checkFavoriteName"
        type="primary">
        
      </el-button>
    </div>
    <!--  -->
    <el-dialog
      :visible.sync="openFavoriteList"
      :show-close="false"
      top="10vh"
      width="80%"
      center>
      <!-- Filter -->
      <div class="filter">
        <el-input
          v-model="searchKeyword"
          placeholder=""
          clearable>
        </el-input>
      </div>
      <el-table
        v-loading="dataLoading"
        :data="tableData"
        :header-row-class-name="`data-table__head`"
        height="350"
        style="width: 100"
        class="data-table favorite-table">
        <template v-for="{label, prop, width, align} in columnConfig">
          <!-- props -->
          <el-table-column
            v-if="prop !== 'actions'"
            :key="prop"
            :label="label"
            :prop="prop"
            :align="align"
            :width="width"
            :formatter="columnDataFormat"
            sortable>
          </el-table-column>
          <!-- functions -->
          <el-table-column
            v-if="prop === 'actions'"
            :key="prop"
            :label="label"
            :align="align"
            :width="width">
            <template slot-scope="scope">
              <el-button
                @click="getFavorite(scope.row.id)"
                type="text">
                
              </el-button>
              <el-button
                @click="deleteFavorite(scope.row.id)"
                type="text">
                
              </el-button>
            </template>
          </el-table-column>
        </template>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="openFavoriteList = false"></el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import dateFormat from 'date-fns/format'
import favoriteApi from '@/api/favorites'
import { mapState, mapGetters, mapActions } from 'vuex'
export default {
  name: 'FavoriteTool',
  props: {
    scrollTop: {
      type: Number
    },
    favoriteData: {
      type: Object
    }
  },
  data () {
    return {
      showDeleteConfirm: false, // 
      showSameNameConfirm: false, // 
      openFavoriteList: false, // 
      searchKeyword: '', // 
      favoriteName: '', // 
      showToolBar: true,
      currentScrollTop: 0,
      //
      favoriteList: [],
      columnConfig: [
        { label: '', prop: 'name', width: null, align: 'left' },
        { label: '', prop: 'client_name', width: null, align: 'left' },
        { label: '', prop: 'campaign_name', width: null, align: 'left' },
        { label: '', prop: 'updated_at', width: '120px', align: 'center' },
        { label: '', prop: 'actions', width: '120px', align: 'center' }
      ]
    }
  },
  computed: {
    ...mapState('root', ['dataLoading']),
    ...mapState('calculation', ['dataSource', 'selectedData']),
    ...mapGetters('root', ['userId']),
    // 
    tableData () {
      return this.searchKeyword
        ? this.favoriteList.filter(favorite => favorite.name.includes(this.searchKeyword))
        : this.favoriteList
    }
  },
  watch: {
    scrollTop () {
      this.scrollEvent()
    },
    // 
    openFavoriteList () {
      if (this.openFavoriteList) {
        this.getFavoriteListData()
      }
    }
  },
  created () {
    this.getFavoriteListData()
  },
  methods: {
    ...mapActions('calculation', ['setDataSource', 'resetSelectedData']),
    // 
    resetConfirm () {
      this.$confirm('?', {
        cancelButtonText: '',
        confirmButtonText: ''
      }).then(() => {
        this.resetSelectedData()
      }).catch(() => {})
    },
    // 
    getFavoriteListData () {
      favoriteApi.getFavoriteListData(this.userId).then(res => {
        this.favoriteList = res.data.data.sort((a, b) => b.id - a.id)
      })
    },
    // 
    checkFavoriteName () {
      if (!this.selectedData.baseInfo.campaignName) {
        return this.$alert('')
      }
      if (!this.selectedData.client_name) {
        return this.$alert('')
      }
      if (!this.favoriteName) {
        return this.$alert('')
      }
      // 
      const checkSameName = this.favoriteList.find(data => data.name === this.favoriteName)
      if (checkSameName) {
        this.$confirm('?', {
          cancelButtonText: '',
          confirmButtonText: ''
        }).then(() => {
          this.createFavorite()
        }).catch(() => {})
      } else {
        this.createFavorite()
      }
    },
    // 
    createFavorite () {
      const favoriteData = {
        name: this.favoriteName,
        campaign_name: this.selectedData.baseInfo.campaignName,
        client_id: this.selectedData.baseInfo.clientId,
        client_name: this.selectedData.baseInfo.clientName,
        agency_id: this.selectedData.baseInfo.agencyId,
        agency_name: this.selectedData.baseInfo.agencyName,
        condition: this.selectedData,
        current_user_id: this.userId
      }
      favoriteApi.createFavorite(favoriteData).then(res => {
        this.showSameNameConfirm = false
        this.getFavoriteListData()
        this.$alert('!')
      })
    },
    // 
    getFavorite (id) {
      favoriteApi.getFavorite(id).then(res => {
        this.$alert('!')
        this.favoriteName = res.data.data.name
        this.setDataSource(res.data.data.condition)
        this.openFavoriteList = false
      })
    },
    // 
    deleteFavorite (id) {
      this.$confirm('?', {
        cancelButtonText: '',
        confirmButtonText: ''
      }).then(() => {
        favoriteApi.deleteFavorite(id).then(res => {
          this.getFavoriteListData()
        })
      }).catch(() => {})
    },
    // formatter
    columnDataFormat (row, column) {
      if (!this.$lodash.isNil(row[column.property])) {
        switch (column.property) {
          case 'updated_at':
            return dateFormat(row[column.property], 'YYYY-MM-DD')
          default:
            return row[column.property]
        }
      }
    },
    scrollEvent () {
      this.showToolBar = (this.scrollTop < this.currentScrollTop) || this.scrollTop === 0
      this.currentScrollTop = this.scrollTop
    }
  }
}
</script>

